<template>
	<div id="vip-redpacket-create">
		<public-header title="开通会员" v-if="!isWeixin"></public-header>
		<div class="content">
			<div class="userinfo">
				<div class="avatar">
					<span class="icon icon-crownfill"></span>
				</div>
				<div class="other flex-1">
					<div class="mobile">{{member.mobile_secret}}</div>
					<div class="status">当前未开通尊享会员</div>
				</div>
			</div>
			<div class="card flex-lr">
				<div>
					<div class="font-bold font-18">{{vipRedpacket.params.title}}</div>
					<div class="font-12 margin-10-t c-gray">{{vipRedpacket.params.placeholder}}</div>
				</div>
				<div>
					{{Lang.dollarSign}}<span class="font-bold font-20">{{vipRedpacket.params.price}}</span>/月
				</div>
			</div>
			<div class="flex-lr tips van-hairline--top van-hairline--bottom">
				<div>红包发放时间</div>
				<div class="c-gray">购买后立即发放</div>
			</div>
			<div class="flex-lr tips van-hairline--bottom">
				<div>会员抵扣红包</div>
				<div class="c-gray">暂无抵用券可用</div>
			</div>
			<div class="flex-center font-12 c-gray margin-15-t">
				<div @click="onTogglePopup('agreement')">常见问题与说明</div>
				<i class="icon icon-right font-12"></i>
			</div>
		</div>
		<div class="button-fixed flex-lr" :class="{disabled: !islegal}">
			<div class="price">
				<span class="c-gray">{{vipRedpacket.params.title}}：</span>
				<span>{{Lang.dollarSign}}</span>
				<span class="font-20">{{vipRedpacket.params.price}}</span>
			</div>
			<div class="pay" @click="onSubmit">去支付</div>
		</div>
		<agreement
			:show="popup.agreement"
			title="常见问题与说明"
			:content="vipRedpacket.rules"
			@agreementHide="onTogglePopup('agreement')"
		>
		</agreement>
		<iloading v-show="preLoading"></iloading>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import Agreement from '@/components/agreement'
	export default {
		data() {
			return {
				member: {},
				vipRedpacket: {
					params: {},
					style: {},
					redpacket: {},
					powers: {}
				},
				popup: {
					agreement: false,
				},
				islegal: false,
				preLoading: true
			}
		},
		components: {
			PublicHeader,
			Agreement
		},
		methods: {
			onLoad() {
				var that = this;
				that.util.request({
					url: 'vipRedpacket/order/create',
				}).then((res) => {
					that.preLoading = false;
					let result = res.data.message;
					if(result.errno) {
						if(result.errno == -2) {
							that.util.$toast(result.message, that.util.getUrl({path: '/plugin/pages/vipRedpacket/index'}), 1500, 'replace');
							return;
						}
						that.util.$toast(result.message);
						return;
					}
					result = result.message;
					that.member = that.util.extend(that.member, result.member);
					that.vipRedpacket = that.util.extend(that.vipRedpacket, result.vipRedpacket);
					that.islegal = true;
				})
			},

			onSubmit() {
				var that = this;
				if(!that.islegal) {
					return false;
				}
				that.islegal = false;
				that.util.request({
					url: 'vipRedpacket/order/create',
					method: 'POST',
				}).then((res) => {
					let result = res.data.message;
					if(result.errno) {
						that.islegal = true;
						that.util.$toast(result.message);
						return;
					}
					result = result.message;
					that.$router.push(that.util.getUrl({path: 'pages/public/pay', query: {order_id: result.id, order_type: 'vipRedpacket'}}))
				})
			},

			onTogglePopup(key) {
				var that = this;
				that.popup[key] = !that.popup[key];
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#vip-redpacket-create #public-header .van-nav-bar, #vip-redpacket-create #agreement .van-nav-bar{
		background-color: #353130!important;
	}
	#vip-redpacket-create .content{
		background-color: #fff;
	}
	#vip-redpacket-create .userinfo{
		width: 100%;
		position: relative;
		background-color: #353130;
		padding: 15px;
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
	#vip-redpacket-create .userinfo .avatar{
		width: 35px;
		height: 35px;
		border-radius: 50%;
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: #47403A;
		margin-right: 10px;
		overflow: hidden;
	}
	#vip-redpacket-create .userinfo .avatar .icon{
		font-size: 20px;
		color: #CDAE9A;
	}
	#vip-redpacket-create .userinfo .mobile{
		color: #C5C4C2;
	}
	#vip-redpacket-create .userinfo .status{
		margin-top: 5px;
		color: #767573;
		font-size: 12px;
	}
	#vip-redpacket-create .card{
		margin: 15px;
		height: 80px;
		padding: 0 15px;
		border-radius: 10px;
		color: #36322D;
		background-image: linear-gradient(90deg, #F7E2CD, #E6B993);
	}
	#vip-redpacket-create .tips{
		padding: 20px 15px;
		font-size: 14px;
	}
	#vip-redpacket-create .button-fixed{
		position: fixed;
		height: 50px;
		z-index: 10;
		left: 15px;
		right: 15px;
		bottom: 15px;
		border-radius: 100px;
		background-color: #fff;
		color: #F4DDD1;
		overflow: hidden;
	}
	#vip-redpacket-create .button-fixed.disabled{
		opacity: .5;
	}
	#vip-redpacket-create .button-fixed .price{
		width: 70%;
		height: 100%;
		line-height: 48px;
		padding-left: 25px;
		font-size: 14px;
		background-color: #2C2626;
	}
	#vip-redpacket-create .button-fixed .pay{
		width: 30%;
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		color: #2C2626;
		background-image: linear-gradient(90deg, #FDECDF, #DFB894);
		font-weight: bold;
	}
	#vip-redpacket-create .button-fixed .pay.disabled{
		background-color: #ccc;
	}
</style>


